/*----------------Suspended---------------------*/

.page-suspended{
  width: 100%;
  height: 100%;
  background-image: url("../img/background.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.container-suspended{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.container-suspended a{
  color: black;
  text-decoration: none;
}

.container-suspended a:hover{
  color:#5c5c5c;
}

.container-suspended a:focus{
  color:#5c5c5c;
}

